.letter-container {
    border-style: solid;
    width: 3rem;
    height: 3rem;
    font-size: 3rem;
    line-height: 3rem;
    align-items: center;
    display: flex;
    justify-content: center;
    margin-right: 0.125rem;
    margin-left: 0.125rem;
    border-color: #e2e2e2;
    font-family: 'Nunito Sans', sans-serif;;
    font-weight: 400;
    font-style: normal;
  }
  
  .filled {
    border-color: black;
    color: black;
  }
  
  .empty {
    border-color: #e2e2e2;
  }
  
  .correct {
    background-color: #79a86b;
    color: aliceblue;
    border-color: #6aaa64;
    animation-name: reveal;
    animation-duration: 1s;
  }
  
  .wrong-place {
    background-color: #c9b458;
    color: aliceblue;
    border-color: #c9b458;
    animation-name: reveal;
    animation-duration: 1s;
  }
  
  .incorrect {
    background-color: #797c7e;
    color: aliceblue;
    border-color: #797c7e;
    animation-name: reveal;
    animation-duration: 1s;
  }
  
  @keyframes reveal {
    from {
      transform: rotateX(-90deg);
    }
    to {
      transform: rotateX(0deg);
    }
  }
  