@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,opsz@0,6..12;1,6..12&display=swap');

html, body {
  margin: 0; /* Remove default margin */
  padding: 0; /* Remove default padding */
  width: 100%; /* Make sure html and body take the full width */
  height: 100%; /* Make sure html and body take the full height */
}

.App {
  text-align: center;
  background-color: #FFFFFF;
  font-family: 'Nunito Sans', sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: center; 
  align-items: center; 
  min-height: 100vh; 
  min-width: 100vw;
}


.App-logo {
  height: 10rem;
  pointer-events: none;
  object-fit: fill;
  align-self: center;
  
}

.App-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #FFFFFF;
  width: 100%;
}

.App-screen {
  width: 100%;
  align-self: flex-start; /* Aligning App-screen to flex-start */
  min-height: 100vh;
}


p {
  color: black;
}

.unselectable {
  user-select: none; /* Standard syntax */
  -moz-user-select: none; /* Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  -webkit-user-select: none; /* Safari */
}

