.keyboard-letter-container {
    border-style: solid;
    width: 3rem;
    height: 3rem;
    font-size: 3rem;
    line-height: 3rem;
    align-items: center;
    display: flex;
    justify-content: center;
    margin-right: 0.25rem;
    margin-left: 0.25rem;
    border-color: #e2e2e2;
    font-family: 'Nunito Sans', sans-serif;;
    font-weight: 400;
    font-style: normal;
  }
    
  .keyboard-filled {
    background-color: #e2e2e2;
    color: black;
  }

  .special {
    background-color: #e2e2e2;
    color: black;
    width: 5rem;
    height: 3rem;
    justify-content: center;
    align-items: center;
    display: flex;
    margin-right: 0.25rem;
    margin-left: 0.25rem;
  }
  
  .correct {
    background-color: #79a86b;
    color: aliceblue;
    border-color: #6aaa64;
    animation-name: reveal;
    animation-duration: 1s;
  }
  
  .wrong-place {
    background-color: #c9b458;
    color: aliceblue;
    border-color: #c9b458;
    animation-name: reveal;
    animation-duration: 1s;
  }
  
  .incorrect {
    background-color: #797c7e;
    color: aliceblue;
    border-color: #797c7e;
    animation-name: reveal;
    animation-duration: 1s;
  }
  
  @keyframes reveal {
    from {
      transform: rotateX(-90deg);
    }
    to {
      transform: rotateX(0deg);
    }
  }


  .KeyboardRow{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    margin-top: 0.25rem;
    margin-bottom: 0.25rem;
    width: calc(100% - 2rem);
    margin-left: 1rem;
    margin-right: 1rem;
}

.Keyboard-container {
  align-items: center;
  align-content: center;
  justify-content: center;
  display: flex;
  flex-direction: column;
  width: 100%;
}
  